import React, { useContext } from 'react'
import ResultContext from '../context/ResultContext'

import Nightsky from '../assets/nightsky-1.jpg'
import ObjectInTheSky from '../assets/star.png'


const Card = ({type, onClick}) => {
  const contextValue = useContext(ResultContext)

  const cardType = (type === 'Sí') ? 'succes' : 'warning'
  const animationImage = (type === "Sí") ? contextValue.animationYes : contextValue.animationNo

  return (
    <section
      className={`card--${cardType}`}
      onClick={onClick}
    >
      <img
        src={Nightsky}
        alt='Nightsky-img'
      />
      <img
        src={ObjectInTheSky}
        alt='object-in-the-Nightsky'
        className={`${animationImage} object-in-the-Nightsky`}
      />
      <h2>
        {type || 'No'}
      </h2>
    </section>
  )
}

export default Card
